.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background : url(https://lh3.googleusercontent.com/-yDQyaJ6qoE8/WlGzgP5BGKI/AAAAAAAANUo/suTspCBwRRgyk4uUxLVJ0fOcdU9PxWehwCL0BGAYYCw/h803/5040864703805102082%253Faccount_id%253D0);
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
  font: Gill Sans;
  color: #D5C5C8
}

.App-intro {
  font-size: large;
  font: Gill Sans;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}



